:root {
    --blend-color: #484668;
    --accent-color: #ffddcc;
}

html {
    scroll-behavior: smooth;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
    line-height: 1.6;
    background-color: #fff;
    color: #222;
}

nav {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 1rem 2rem;
    background-color: #fff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
} 

.nav-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 1200px;
}

.logo {
    font-size: 1.5rem;
    font-weight: bold;
    color: #222;
    text-decoration: none;
}

.nav-menu {
    display: flex;
    list-style: none;
}

.nav-menu li {
    margin-left: 1.5rem;
}

.nav-menu a {
    color: #222;
    text-decoration: none;
    transition: color 0.3s;
}

.nav-menu a:hover {
    color: var(--accent-color);
}

.hamburger {
    display: none;
    flex-direction: column;
    justify-content: space-around;
    width: 24px;
    height: 18px;
}

.hamburger span {
    display: block;
    width: 100%;
    height: 2px;
    background-color: #222;
    transition: all 0.3s;
}

.nav-menu.show {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background-color: #fff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    z-index: 10;
}

.nav-menu.show li {
    margin: 1rem 0;
}

.hero {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-color: #fff;
    line-height: 4em;
}

.blend {
    mix-blend-mode: exclusion;
    color: var(--blend-color);
}

.circle-image-wrapper {
    height: clamp(200px, 40vw, 320px);
    width: clamp(200px, 40vw, 320px);
    border-radius: 100%;
    background-color: var(--blend-color);
    margin-bottom: 1rem;
    margin-top: 2rem;
    overflow: hidden;
    border: 4px solid white;
}

.circle-image-wrapper img {
    width: 100%;
}
  
.hero h1 {
    font-size: 5rem;
    margin-bottom: 1rem;
}

.hero h2 {
    font-size: 2rem;
    margin-bottom: 1rem;
}

.chevron {
    animation: moveUpDown 2s ease-in-out infinite;
}

.about {
    padding: 2rem;
    opacity: 0;
}

.about.visible {
    animation: slideInLeft 1s ease-out forwards;
}
  

.about-content {
    max-width: 1000px;
    margin: 0 auto;
}

.about-content h2 {
    font-size: 2rem;
    margin-bottom: 1rem;
}

.about-content p {
    font-size: 1.1rem;
    line-height: 1.5;
    margin-bottom: 1rem;
}

.projects {
    padding: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column nowrap;
    width: 100%;
    margin: auto;
}

.projects h2, .projects h3 {
    width: 100%;
    max-width: calc(1000px);
}

.featured {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    flex-flow: row-reverse wrap;
}

.featured-content {
    display: flex;
    flex-flow: column wrap;
    width: clamp(200px, 80vw, 600px);
    margin-bottom: 2rem;
}

.description {
    margin-top: 1rem;
    margin-bottom: 1rem;
    margin-right: 1rem;
}

.tech-used {
    display: flex;
    flex-flow: row wrap;
    gap: 0.5rem;
}

.tech {
    white-space: nowrap;
    background-color: #484668;
    border-radius: 12px;
    color: white;
    padding: 8px 16px;
}

.tech-tag {
    background-color: #ccc;
    border-radius: 5px;
    padding: 0.25rem 0.5rem;
}

.featured-project-img-wrapper {
    width: clamp(200px, 80vw, 400px);
}

.featured-project-img-wrapper img {
    width: 100%;
    height: auto;
}

.other-projects {
    margin: 1em;
    margin-left: -1em;
    padding-bottom: 1em;
    display: flex;
    overflow-x: scroll;
    gap: 1rem;
    max-width: clamp(200px, 80vw, 1000px);
    /* Firefox scrollbar support */
    scrollbar-width: thin;
    scrollbar-color: #484668 #f0f0f0;
}

/* Webkit-based browser scrollbar styling */
.other-projects::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

.other-projects::-webkit-scrollbar-track {
    background-color: #f0f0f0;
    border-radius: 5px;
}

.other-projects::-webkit-scrollbar-thumb {
    background-color: #484668;
    border-radius: 5px;
}

.other-projects::-webkit-scrollbar-thumb:hover {
    background-color: #3c3a62;
}
  
.project-card {
    background-color: #f5f5f5;
    border-radius: 5px;
    padding: 1rem;
    width: 250px;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.project-card img {
    width: 100%;
    height: auto;
    border-radius: 5px;
    margin-bottom: 1rem;
}

.project-card h4 {
    margin-bottom: 1rem;
}

.project-card .links {
    display: flex;
    flex-direction: row;
    gap: 0.5rem;
}

.project-card .links a {
    padding: 0.25rem 0.5rem;
    background-color: #484668;
    border-radius: 5px;
    color: white;
    text-decoration: none;
    text-align: center;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
}

.skills {
    position: relative;
    overflow: hidden;
}

.skills-parallax {
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 4rem 0;
}

.skills-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
    padding: 2rem;
    position: relative;
    z-index: 1;
    opacity: 0;
}

.skills-container.visible {
    animation: fadeIn 1.3s ease-out forwards
}

.skill {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.skill i {
    font-size: 3rem;
    margin-bottom: 0.5rem;
}

.skill p {
    font-size: 1.1rem;
    margin: 0;
}

.contact {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
}

.contact h2 {
    margin-bottom: 1rem;
}

.contact form {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 500px;
}

.contact label {
    margin-top: 1rem;
}

.contact input, .contact textarea {
    width: 100%;
    padding: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-family: inherit;
}

.contact textarea {
    resize: vertical;
}

.contact input[type="submit"] {
    margin-top: 1rem;
    background-color: #484668;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    padding: 0.5rem;
}

.contact input[type="submit"]:hover {
    background-color: #3c3a62;
}  

.submit-button {
    background-color: #484668;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    padding: 0.5rem 1rem;
    margin-top: 1rem;
    font-size: 1rem;
    transition: background-color 0.3s;
}

.submit-button:hover {
    background-color: #3c3a62;
}

footer {
    background-color: white;
    color: black;
    padding: 1rem;
    text-align: center;
}

footer p {
    margin: 0;
}  

.back-to-top {
    display: none;
    position: fixed;
    bottom: 20px;
    right: 20px;
    background-color: #73d4fe;
    color: #ffffff;
    border: none;
    padding: 10px 20px;
    font-size: 14px;
    cursor: pointer;
    border-radius: 4px;
    z-index: 100;
}

.back-to-top.visible {
    display: block;
}

@media (max-width: 768px) {
    .nav-menu {
        display: none;
    }
  
    .hamburger {
        display: flex;
    }
    .skills-container {
        grid-template-columns: repeat(2, 1fr);
    }
}

@keyframes slideInLeft {
    from {
        transform: translateX(-100%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes moveUpDown {
    0%, 100% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-10px);
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}